import { OpenBet, HistoryBet } from '../../models/BetOrder';
import moment from 'moment'
import permutation from '../../utils/permutation'
import multiDimensionalUnique from '../../utils/multiDimensionalUnique'
import _ from 'lodash'


const confirmResultReverse = async (openBet) => {

	const allEventsReview     = _.every(openBet.eventOdds, { status: 'Review' })
	const allEventsPending    = _.every(openBet.eventOdds, { status: 'Pending' })
	const eventsHaveReview    = _.some(openBet.eventOdds, { status: 'Review' })
	const eventsHavePending   = _.some(openBet.eventOdds, { status: 'Pending' })


	let betOrderStatus = 'TBD'
	let resultAmount = 0

	const teamLength = _.range(openBet.eventOdds.length)
	const inOrder = permutation(teamLength)
	const orderList = multiDimensionalUnique(inOrder)
	const { betAmount } = openBet.wagerDetail


	if(!eventsHaveReview && !allEventsReview && !allEventsPending && !eventsHavePending){
	

		orderList.forEach(list => {
			for(let idx of list){
				const { status, betDetail } = openBet.eventOdds[idx]
				switch(status){
			 		case 'Won':
			 			let winAmount = 0
			 			if(betDetail.oddLine > 0){
			 				winAmount = Math.round(betAmount * betDetail.oddLine / 100)
			 			}else{
			 				winAmount = Math.round(betAmount / Math.abs(betDetail.oddLine) * 100)
			 			}
			 			resultAmount += winAmount
			 			break;
			 		case 'Lost':
			 			resultAmount -= betAmount
			 			return
			 		case 'Push':
			 		case 'Canceled':
			 		case 'Postponed':
			 			if(openBet.orderType === 'ActionReverse'){
			 				break;
			 			}
			 			if(openBet.orderType === 'WinReverse'){
			 				return;
			 			}
			 		default:
			 			break;
				}
			}
		})



		if(resultAmount === 0){
			betOrderStatus = 'Push'
		}

		if(resultAmount > 0){
			betOrderStatus = 'Won'
		}

		if(resultAmount < 0){
			betOrderStatus = 'Lost'
		}

	}





	if(betOrderStatus !== 'TBD'){
		const newHistoryBet = new HistoryBet({
			orderNumber: openBet.orderNumber,
			orderType: openBet.orderType,
			owner: openBet.owner,
			wagerDetail: openBet.wagerDetail,
			status: betOrderStatus,
			resultAmount: resultAmount,
			eventOdds: openBet.eventOdds,
			createdAt: openBet.createdAt,
			closedAt: moment()
		})
	
		await newHistoryBet.save()
		console.log('saved history straight bet' + newHistoryBet.orderNumber)
		await OpenBet.findOneAndRemove({ _id: openBet._id })
		console.log('deleted openbet ' + openBet.orderNumber)
		return true

	}

}

export default confirmResultReverse



